import { Component, OnInit, ElementRef } from '@angular/core';
import { Router } from '@angular/router';

import { LoginService } from '../../services/login.service';
import { UserService } from '../../services/user.service';
import { User } from '../../models/user';
import { LocalStorageService }  from '../../services/local-storage.service';

declare var $: any;

@Component({
    selector: 'app-login-cmp',
    templateUrl: './login.component.html',
    styleUrls: ['./login.component.css']
})

export class LoginComponent implements OnInit {
    test: Date = new Date();
    private toggleButton: any;
    private sidebarVisible: boolean;
    private nativeElement: Node;
    public model = {'email':'', 'password':'', 'isAdmin':''};
    public currentEmail: any;
    private currentIsAdmin: any;
    private user: User;


    constructor(private element: ElementRef, private loginService: LoginService, 
        private userService: UserService, private router: Router, private localStorageService: LocalStorageService) {
        this.nativeElement = element.nativeElement;
        this.sidebarVisible = false;

        this.currentEmail=localStorage.getItem("currentEmail");
        this.currentIsAdmin=localStorage.getItem("currentIsAdmin");
    }

    ngOnInit() {
        var navbar : HTMLElement = this.element.nativeElement;
        this.toggleButton = navbar.getElementsByClassName('navbar-toggle')[0];

        setTimeout(function() {
            // after 1000 ms we add the class animated to the login/register card
            $('.card').removeClass('card-hidden');
        }, 700);
    }
    
    sidebarToggle() {
        var toggleButton = this.toggleButton;
        var body = document.getElementsByTagName('body')[0];
        var sidebar = document.getElementsByClassName('navbar-collapse')[0];
        if (this.sidebarVisible == false) {
            setTimeout(function() {
                toggleButton.classList.add('toggled');
            }, 500);
            body.classList.add('nav-open');
            this.sidebarVisible = true;
        } else {
            this.toggleButton.classList.remove('toggled');
            this.sidebarVisible = false;
            body.classList.remove('nav-open');
        }
    }

    onSubmit() {
        this.loginService.sendCredential(this.model).subscribe(
            data => {
                localStorage.setItem("token", JSON.parse(JSON.stringify(data))._body);
                this.loginService.sendToken(localStorage.getItem("token")).subscribe(
                    data => {
                        this.currentEmail=this.model.email;
                        this.currentIsAdmin=this.model.isAdmin;
                        localStorage.setItem("currentEmail", this.model.email);

                        this.userService.getUserByEmail(localStorage.getItem("currentEmail")).subscribe(
                            user => {
                                this.user = JSON.parse(JSON.parse(JSON.stringify(user))._body);
                                this.loginService.userLogado = this.user;
                                this.localStorageService.userLogado = this.user;
                                console.log(this.localStorageService.userLogado);

                                let tostring: string = "";
                                if(this.user.isAdmin){
                                    tostring = "true";
                                }
                                
                                localStorage.setItem("currentIsAdmin", tostring);
                                localStorage.setItem("currentNome", this.user.nome);
                                localStorage.setItem("currentSobrenome", this.user.sobrenome);
                                
                                if(this.user.isAdmin) {
                                    this.router.navigate(['/tables/datatables.net'], {queryParams: {}});
                                } else {
                                    localStorage.setItem("currentEmailUserEdit", this.user.email);
                                    this.router.navigate(['/observador/timeline'], {queryParams: {}});

                                }
                            },
                            error => console.log(error)
                            );
                        this.model.email='';
                        this.model.password='';
                        this.model.isAdmin='';
                    },
                    error => { 
                        console.log(error);
                        alert("Login ou senha inválidos");
                    }
                    );
            },

            error => { 
                console.log(error);
                alert("Login ou senha inválidos");
            }

            );

    }

}



